@import "https://fonts.googleapis.com/css?family=Nunito:400,300,700|Roboto:300,400,700|Roboto+Mono";
@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.3.0/css/all.min.css";
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
}

.news-app {
  color: #333;
}

h2, figure {
  margin: 0;
  padding: 0;
}

.header {
  font-family: Nunito, sans-serif;
  box-shadow: 0 0 6px rgba(0, 0, 0, .12);
}

.header__wrapper {
  width: 94%;
  max-width: 700px;
  height: 4em;
  margin: 0 auto;
  padding: 0 3%;
  line-height: 4em;
  display: flex;
}

.header__logo {
  opacity: .8;
  margin-right: .5em;
  font-size: 1.3em;
  line-height: 3.2em;
  display: inline-block;
}

.header__brand {
  font-size: 1.3em;
}

.search {
  max-width: 600px;
  margin: 2.5em auto 1em;
  padding: 0 3%;
  font-family: Nunito, sans-serif;
}

.search__wrapper {
  height: 3em;
  border-bottom: 1px solid rgba(0, 0, 0, .12);
  justify-content: space-between;
  display: flex;
  position: relative;
}

.search__field {
  width: 100%;
  text-align: center;
  background: none;
  border: none;
  margin: 0 1em;
  font-size: 1.5em;
  font-weight: 300;
}

.search__field:focus-visible {
  outline: 0;
}

.search__field::-ms-input-placeholder {
  opacity: .3;
  font-weight: 300;
}

.search__field::placeholder {
  opacity: .3;
  font-weight: 300;
}

.search__icon {
  width: 1em;
  height: 1.4em;
  display: block;
}

.search__icon.fa-magnifying-glass {
  font-size: 1.2em;
  display: inline;
}

.search__icon.fa-circle-notch {
  display: none;
}

.search__btn {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0 .4em;
  line-height: 1.5em;
}

.search_loading.search__icon, .search_loading.search__icon.fa-magnifying-glass, .search_loading.search__icon.fa-circle-notch {
  animation-name: spin;
  animation-duration: .8s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  display: block;
}

.articles {
  max-width: 700px;
  flex-direction: column;
  gap: 2em;
  margin: 0 auto;
  padding: 4em 3%;
  display: flex;
}

.article {
  width: 100%;
  box-sizing: border-box;
  border-radius: 2px;
  margin: 0 auto;
  padding: 2em 2.5em;
  font-family: Roboto, sans-serif;
  position: relative;
  box-shadow: 0 1px 6px rgba(0, 0, 0, .12), 0 1px 4px rgba(0, 0, 0, .12);
}

.article__title {
  text-align: center;
  margin: .8em 0 1em;
  font-size: 1.4em;
}

.article__publish-date, .article__author {
  width: 50%;
  text-align: right;
  text-transform: uppercase;
  opacity: .8;
  background: linear-gradient(to right, rgba(255, 255, 255, 0), #eeeded);
  padding: .3em 3% .2em 0;
  font-family: Roboto Mono, sans-serif;
  font-size: .8em;
  font-weight: 300;
  position: absolute;
  top: .8em;
  right: 0;
}

.article__author {
  text-align: left;
  background: linear-gradient(to left, rgba(255, 255, 255, 0), #eeeded);
  padding-left: 3%;
  left: 0;
  right: auto;
}

.article__image {
  filter: grayscale();
  transition: filter .3s;
}

img {
  width: 100%;
  height: auto;
}

.article__content {
  overflow-wrap: anywhere;
  margin: 1em 0;
}

.article__time-for-reading {
  text-align: right;
  font-family: Roboto Mono, sans-serif;
  font-size: .8em;
}

.article:hover, .article__image {
  filter: none;
}

.load-more {
  display: flex;
}

.load-more__btn {
  width: 30%;
  text-transform: uppercase;
  cursor: pointer;
  background: rgba(0, 0, 0, .07);
  border: none;
  margin: 0 auto;
  padding: .5em 0;
  font-size: .8em;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

